<template>
    <div class="m-switch">
        <div class="m-switch-item" v-for="item in items" @click="handlerItem(item)"
             :class="{'active': item.value === value}">
            {{item.name}}
        </div>
    </div>
</template>

<script>
    export default {
        name: "m-switch",
        props: {
            value: {},
            items: {
                type: Array,
                default: () => []
            }
        },
        methods: {
            handlerItem(item) {
                this.$emit('input', item.value)
            }
        }
    }
</script>

<style lang="stylus" scoped>

    .m-switch
        display flex
        align-items center
        justify-content center
        width 100%
        height 25px
        padding 2px
        border-radius 3px
        box-shadow 0 0 2px #2196f3
        overflow hidden
        .m-switch-item
            padding 0 15px
            height 20px
            line-height 20px
            font-size 12px
            border-radius 2px
            transition all .3s
            &.active
                color #fff
                background-color #2196f3
</style>



